body {
    font-family: $font-stack;
}

.heading {
    font-weight: $heading-weight;
}

.subheading {
    font-weight: $subheading-weight;
}

.heading,
.subheading {
    margin: 0;
    margin-bottom: $heading-margin;
}

.paragraph {
    margin: 0;
    margin-bottom: $paragraph-margin;
    line-height: $paragraph-line-height;
}

.link {
    color: #41c6a7;
    text-decoration: none;

    &:hover {
        @extend .text-underline;
    }
}

.text-decoration-none {
    text-decoration: none;
}

.text-underline {
    text-decoration: underline;
}

.text-italic {
    font-style: italic;
}

.text-uppercase {
    text-transform: uppercase;
}

.text-lowercase {
    text-transform: lowercase;
}

.text-capitalize {
    text-transform: uppercase;
}

.text-thin {
    font-weight: 100;
}

.text-extralight {
    font-weight: 200;
}

.text-light {
    font-weight: 300;
}

.text-normal {
    font-weight: 400;
}

.text-medium {
    font-weight: 500;
}

.text-semibold {
    font-weight: 600;
}

.text-bold {
    font-weight: 700;
}

.text-extrabold {
    font-weight: 800;
}

.text-heavy {
    font-weight: 900;
}

.text-white {
    color: $white;
}

.text-black {
    color: $black;
}

.text-lightgrey {
    color: $lightgrey;
}

.text-darkgrey {
    color: $darkgrey;
}

.text-sans-serif {
    font-family: $sans-serif;
}

.text-serif {
    font-family: $serif;
}

.text-monospace {
    font-family: $monospace;
}

.text-spacing-none {
    margin-bottom: 0;
}

.text-spacing-relative {
    margin-bottom: 1rem;
}

@include each-breakpoint() {
    @include global-property(text-left) {
        text-align: left;
    }

    @include global-property(text-center) {
        text-align: center;
    }

    @include global-property(text-right) {
        text-align: right;
    }

    @include global-property(text-justify) {
        text-align: justify;
    }
    
    @include global-property(text-size-1) {
        font-size: $text-size-1;
    }

    @include global-property(text-size-2) {
        font-size: $text-size-2;
    }
    
    @include global-property(text-size-3) {
        font-size: $text-size-3;
    }
    
    @include global-property(text-size-4) {
        font-size: $text-size-4;
    }
    
    @include global-property(text-size-5) {
        font-size: $text-size-5;
    }
    
    @include global-property(text-size-6) {
        font-size: $text-size-6;
    }
    
    @include global-property(text-size-7) {
        font-size: $text-size-7;
    }
    
    @include global-property(text-size-8) {
        font-size: $text-size-8;
    }
}
